import React from "react";

function Card(props) {
  return (
    <>
      <div className="col-lg-4">
        <div id="card" className="card mb-5 mb-lg-0">
          <div className="card-body">
            <h5 className="card-title text-muted text-uppercase text-center">
              {props.data.productName}
            </h5>
            <h6 className="card-price text-center">
              {props.data.productCost}
              <span className="period">/month</span>
            </h6>
            <hr />
            <ul className="fa-ul">
              <li>
                <span className="fa-li">
                  <i className="fas fa-check"></i>
                </span>
                <strong>{props.data.productUser}</strong>
              </li>
              <li>
                <span className="fa-li">
                  <i className="fas fa-check"></i>
                </span>
                {props.data.productStorage}
              </li>
              <li>
                <span className="fa-li">
                  <i className="fas fa-check"></i>
                </span>
                {props.data.productPublic}
              </li>
              <li>
                <span className="fa-li">
                  <i className="fas fa-check"></i>
                </span>
                {props.data.productAccess}
              </li>
              <li className={props.data.productPrivate.classType}>
                <span className="fa-li">
                  <i className={props.data.productPrivate.icon}></i>
                </span>
                {props.data.productPrivate.sub}
              </li>
              <li className={props.data.productSupport.classType}>
                <span className="fa-li">
                  <i className={props.data.productSupport.icon}></i>
                </span>
                {props.data.productSupport.sub}
              </li>
              <li className={props.data.productSubDomain.classType}>
                <span className="fa-li">
                  <i className={props.data.productSubDomain.icon}></i>
                </span>
                {props.data.productSubDomain.sub}
              </li>
              <li className={props.data.productStatus.classType}>
                <span className="fa-li">
                  <i className={props.data.productStatus.icon}></i>
                </span>
                {props.data.productStatus.sub}
              </li>
            </ul>
            <a
              href="#"
              id="btn"
              className="btn btn-block btn-primary text-uppercase"
            >
              Button
            </a>
          </div>
        </div>
      </div>
    </>
  );
}

export default Card;

